@import url("https://fonts.googleapis.com/css2?family=Borel&family=Montez&family=Nunito:ital,wght@0,300;0,400;0,500;0,600;0,700;1,700&display=swap");

* {
  margin: 0;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}
button {
  /* font-family: "Montserrat", sans-serif; */
  font-family: Nunito;
}
body {
  font-family: "Nunito";
  background-color: #f8f8f8;
}

.container {
  max-width: 1140px;
  margin: 0 auto;
  width: 95%;
}

::-webkit-scrollbar {
  display: none;
  overflow-y: hidden;
}
